.menu-container {
   position: relative;
   position: fixed;
   top:0;
   left: 0;
   width: 100vw;
   height: 100vh;
   padding:60px 23px;
   background-color:rgba(225, 121, 146, 1);
   z-index: 999;
   transform: translateX(100%);
   transition: transform 250ms ease-in-out;
   @media screen and(min-width:$tablet) { 
    width: 30vw;
      transform: translateX(350%);
      transition: transform 550ms ease-in-out;
      
     }
   }

   .menu-container.is-open{
      transform: translateX(0);
         @media screen and(min-width:$tablet) { 
            transform: translateX(233%);
     
      
      
     }
    }
    .menu-toggle-close {
       position: absolute;
       background:inherit;
        border: 0;
       fill:$main-text-color;
       top: 20px;
       right: 30px;

       &:focus,
       &:hover {
          fill:rgb(179, 235, 69);
          cursor: pointer;
       }
    }

  

   .menu__button {
       padding-top:14px ;
       padding-bottom:14px ;
       margin-bottom: 32px;
       width: 100%;
       background-color: $main-text-color;   
        border: none;
       border-radius: 50px;
      font-weight: bold;
      font-size: 16px;
      line-height: 1.312;
     display: flex;
     align-items: center;
     justify-content: center;
      
      color:$title-text-color;
     
      &:focus,
      &:hover {
         background-color: $bg-color-btn-hover;
         cursor: pointer;
      }
    }
    .menu__button-icon{
       fill:$title-text-color;
       margin-left: 20px;
    }
    .menu__list {
       display: inline;
      list-style: none;
     
   }
   //  .menu__item:not(:last-child) {
   //    margin-bottom: 23px;
    
   //    }
      .menu__link{
         display: block;
         text-decoration: none;
         font-family: DM Sans;
         font-style: normal;
         font-weight: bold;
         font-size: 16px;
         line-height: 1.312;
         color: $main-text-color;
         padding: 10px;
         border-top: 1px solid #E18298;
           &:focus,
           &:hover {
              color:rgb(179, 235, 69);
           }
         &--red {
           color: $title-text-color;
           
         }
         &:last-child{
            border-bottom: 1px solid #E18298;
         }
      }

   